<template>
    <view>
        <!-- 上传一张好看的头像吧 -->
        <view class="box">
            <button class="box-icon" :border="false" open-type="chooseAvatar" @chooseavatar="chooseAvatar">

                <view style="position: relative;">

                    <image class="box-icon-img" :src="src"></image>
                    <view class="box-icon-img-abs-view">

                        <image class="box-icon-img-abs" mode="widthFix" :src="$kt.file.byPath('image/icon/album.svg')">
                        </image>
                    </view>
                </view>
            </button>
            <view 
            v-if="false"
            style="height: 20rpx;"></view>
            <view
            v-if="false"
            class="box-text">
                上传一张好看的头像吧
            </view>

            <view
            v-if="false"
            style="height: 40rpx;"></view>
            <view
            v-if="false"
            style="padding: 50rpx 70rpx 50rpx 70rpx;">
                <kt-button ref="nextBtn" @click="toNext">下一步</kt-button>
            </view>

        </view>
    </view>
</template>

<script>
export default {
    data() {
        return {
            src: ""
        }
    },
    created() {
        this.avatarInit();
    },
    methods: {
        async avatarInit() {
            var fileIdOfAvatar = this.$selfInfo.get().fileIdOfAvatar;
            if (fileIdOfAvatar != null) {
                var src = await this.getImageInfo(this.$kt.file.byGroupCodeAndId("avatar", this.$selfInfo.get().fileIdOfAvatar));
                this.src = src.path;
                this.$emit("change",this.src);
                return false;
            }

            var src = await this.getImageInfo(this.$kt.file.byPath('image/icon/rabbitAvatar.png'));
            this.src = src.path;
            this.$emit("change",this.src);
        },
        chooseAvatar(e) {
            this.src = e.detail.avatarUrl;
            this.$emit("change",this.src);
        },
        getImageInfo(imgSrc) {
            return new Promise((resolve, reject) => {
                uni.getImageInfo({
                    src: imgSrc,
                    success: (image) => {
                        resolve(image);
                    },
                    fail: (err) => {
                        reject(err);
                        console.log('获取图片失败', err)
                    }
                });
            });
        }
    }
}
</script>

<style lang="scss" scoped>
.box {
    // 黑色滤镜
    position: relative;
    text-align: center;
    width: 100%;
    font-size: 40rpx;

    .box-icon {
        width: 230rpx;
        height: 230rpx;
        margin: 0 auto;
        padding: 0rpx;
        background-color: #fff;


        .box-icon-img-abs-view {
            position: absolute;
            display: inline-block;
            width: 70rpx;
            height: 70rpx;
            right: 0rpx;
            bottom: 0rpx;
            border-radius: 55%;
            background-color: #fff;
            border: 10rpx solid #fff;

            .box-icon-img-abs {
                position: absolute;
                display: inline-block;
                width: 70rpx;
                height: 70rpx;
                right: 0rpx;
                bottom: 0rpx;
                border-radius: 55%;
                background-color: #fff;
                border: 10rpx solid #fff;
            }
        }

        .box-icon-img {
            width: 200rpx;
            height: 200rpx;
            border-radius: 50%;
        }
    }

    .box-text {
        color: #333;
        font-weight: bold;
    }
}

.box-icon::after {
    border: none;
}

.box-icon:active {
    .box-icon-img-abs {
        opacity: 0.7;
    }

}
</style>
